import React from "react"
import clsx from "clsx"
import { Link } from "react-router-dom";
import { useTheme } from '@material-ui/core/styles';
import {Avatar, Box, Divider, IconButton, Grid, List} from '@material-ui/core'
import {ListItem, ListItemIcon, ListItemText, Drawer, Typography} from "@material-ui/core"
import { red, blue} from '@material-ui/core/colors'
import VpnKeyIcon from '@material-ui/icons/VpnKey';
import HomeIcon from '@material-ui/icons/Home';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import DashboardIcon from '@material-ui/icons/Dashboard';
import MenuBookIcon from '@material-ui/icons/MenuBook';
import VpnKeyOutlinedIcon from '@material-ui/icons/VpnKeyOutlined';

const MyDrawer = ({ title, open, onClose, onItemClick, variant, t}) => {
  const user = JSON.parse(localStorage.getItem('user'));
  const theme=useTheme

  
  const PublicList=()=>{
	  return (
	  <React.Fragment>
	  		<Box mt={2} />
		<Grid
			container
			spacing={1}
			justify="center"
			alignItems="center"
		>
		  <Avatar alt='login' >
			  <VpnKeyIcon color="primary" />
		  </Avatar>
		  <Avatar alt='login' >
			  {t(title)}
		  </Avatar>
		  <Avatar> {title.slice(0, 1)}</Avatar>
		</Grid> 
		<Box mt={2} />
	  <Divider />
		<div style={{width: 250}}>
		   <ListItem 
				button 
				component={Link}
				to="/"
				onClick={onItemClick("Home")}>
			  <ListItemIcon>
				<HomeIcon style={{ color: red[500] }}/>
			  </ListItemIcon>
			  <ListItemText primary={t("Home")} />
			</ListItem>	
			<ListItem 
				button
					component={Link}
					to="/login"			
					onClick={onItemClick("Login")}>	
			  <ListItemIcon>
				<VpnKeyOutlinedIcon style={{ color: blue[500] }} />
			  </ListItemIcon>
			  <ListItemText primary={t("Login")} />
			</ListItem>
			<ListItem 
				button
					component={Link}
					to="/signup"		
					onClick={onItemClick("Signup")}>	
			  <ListItemIcon>
					<ExitToAppIcon color="secondary"/>
			  </ListItemIcon>
			  <ListItemText primary={t("Signup")} />
			</ListItem>
		 </div>
        <Divider />

			</React.Fragment>
	  )
  }

  const AuthList=()=>{
	  return (
	  <React.Fragment>
	  		<Box mt={2} />
		<Grid
			container
			spacing={1}
			justify="center"
			alignItems="center"
		>
		  <Avatar alt={user.user.firstname} style={{ backgroundColor: blue[500] }}>
			 {user.user.firstname}
		  </Avatar>
		  <Typography>
		  &nbsp;{user.user.email}
		  </Typography>
		</Grid> 
		<Box mt={2} />
	  <Divider />
		<div style={{width: 250}}>
			<ListItem 
				button
					component={Link}
					to="/dashboard"		
					onClick={onItemClick("Dashboard")}>	
			  <ListItemIcon>
					<DashboardIcon style={{ color: red[500] }}/>
			  </ListItemIcon>
			  <ListItemText primary={t("Dashboard")} />
			</ListItem>	  
			<ListItem 
				button
					component={Link}
					to="/clinicmembers"		
					onClick={onItemClick("Show Clinic members")}>	
			  <ListItemIcon>
						{'\u{1F930}'}
			  </ListItemIcon>
			  <ListItemText primary={t("Show Clinic members")} />
			</ListItem>	  
			<ListItem 
				button
					component={Link}
					to="/patients"		
					onClick={onItemClick("Show Child and Family")}>	
			  <ListItemIcon>
						{'\u{1F46A}'}
			  </ListItemIcon>
			  <ListItemText primary={t("Show Child and Family")} />
			</ListItem>	  
			<ListItem 
				button
					component={Link}
					to="/opdscreens"		
					onClick={onItemClick("Show collection OPDscreen")}>	
			  <ListItemIcon>
						{'\u{1F46A}'}
			  </ListItemIcon>
			  <ListItemText primary={t("Show collection OPDscreen")} />
			</ListItem>	
			</div>
        <Divider />

			</React.Fragment>
	  )
  }

  
  return (

	  <Drawer
        open={open}
		onClose={onClose}
      >	
		<List disablePadding>
			{user?
			<AuthList/>:
			<PublicList/>
			}
		</List>
      </Drawer>
  )
};

export default MyDrawer;
